.footer {
  font-size: 1.4rem;
  text-align: center;
  background-color: $color-pizazz;
}

.footer__description {
  color: white;
  width: 34rem;
  padding: 1rem;
  margin: 0 auto;
}

.footer__link {
    padding: 1.5rem;
    display: inline-block;
    color: $color-astral;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
}

.social__icon {
    display: inline-block;
    width: 15rem;
    height: 15rem;
    background-image: url("asset/image/social-icons-sprite.png");
}

.social__item {
    &:nth-child(2) .social__icon {
      background-position-y: -15rem;
    }
    &:nth-child(3) .social__icon {
      background-position-y: -30rem;
    }
    &:nth-child(4) .social__icon {
      background-position-y: -45rem;
    }
}